const burger = document.querySelector(".burger");
const mobileUL = document.querySelector(".mobileUL");
const preloader = document.querySelector("#preloader");
const images = document.querySelectorAll(".sampleGrid img");

burger.addEventListener("click", () => {
  burger.classList.toggle("active");
  if (burger.classList.contains("active")) {
    mobileUL.classList.add("triggered");
  } else {
    mobileUL.classList.remove("triggered");
  }
});

window.addEventListener("load", () => {
  preloader.classList.add("preloadFinished");
});
